import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom'

const NewsItemBlock = styled.div`
    display:flex; margin:10px; width:48%; max-height:110px; overflow:hidden;
    border:1px solid #e1e1e1; padding:10px;
    .thumbnail { width:30%; overflow:hidden;
        padding-right:20px;
        img { min-height:100%; }
    }
    .contents { width:70%; overflow:hidden;
        h2 { height:26px; overflow:hidden; font-size:1.4rem }
        p { padding-top:5px; }
    }
`;

const NewsItem = ({article}) => {
    const {title, description, urlToImage } = article;

    return (
        <NewsItemBlock>
            { urlToImage && (
                <div className="thumbnail">
                    <Link to={{ pathname:'/news/detail', state:{urlToImage, title, description}}}>
                        <img src={urlToImage} alt={title} />
                    </Link>
                </div>
                )
            }
            { description && (
                <div className="contents">
                    <h2>
                        {title}
                    </h2>
                    <p>{description.slice(0,80)}...</p>
                </div>)
            }
        </NewsItemBlock>
    );
};

export default NewsItem;