import React , { useState } from 'react';
import cn from 'classnames';
import {Link} from 'react-router-dom';
import { GiHamburgerMenu } from "react-icons/gi";
import { FaUserCircle } from "react-icons/fa";
import './Header.scss';

const Header = () => {

    const [on, setNav1] = useState(false);
    const [on2, setNav2] = useState(false);
    const [on3, setNav3] = useState(false);
    const [opener, setOpener] = useState(false);
    const openclose = () => {
        setOpener(!opener)
    }


    return (
        <div className="headerWrap">
            <div className="row">
                <h1 className="logo">
                    <Link to="/" className="lnk">
                    </Link>
                </h1>
                <nav className="nav">
                    <ul className="depth1">
                        <li onMouseEnter={ () => setNav1(true) } onMouseLeave={ () => setNav1(false) } >
                            종류별레시피
                            <ul class={cn("depth2", {on})}>
                                <li><Link to='/' className="lnk">밥</Link></li>
                                <li>반찬</li>
                                <li>국물</li>
                                <li>면</li>
                                <li>디저트</li>
                                <li>분식</li>
                                <li>샐러드</li>
                                <li>음료</li>
                                <li>기타</li>
                            </ul>
                        </li>
                        <li onMouseEnter={ () => setNav2(true) } onMouseLeave={ () => setNav2(false) } >
                            재료별레시피
                            <ul class={cn("depth2", {on2})}>
                                <li><Link to='/' className="lnk">밥</Link></li>
                                <li>쇠고기</li>
                                <li>돼지고기</li>
                                <li>닭고기</li>
                                <li>채소</li>
                                <li>해물</li>
                                <li>계란</li>
                                <li>유제품</li>
                                <li>기타</li>
                            </ul>
                        </li>
                        <li onMouseEnter={ () => setNav3(true) } onMouseLeave={ () => setNav3(false) } >
                            고객서비스
                            <ul class={cn("depth2", {on3})}>
                                <li><Link to='/news' className="lnk">뉴스보기</Link></li>
                                <li>고객한마디</li>
                                <li>공지사항</li>
                                <li>견학공장위치</li>
                            </ul>
                        </li>
                    </ul>
                </nav>
                <div className="mobileNav">
                    <button onClick={openclose}>
                        <GiHamburgerMenu className="ham" />
                        <FaUserCircle className="person" />
                    </button>
                    <div className={cn("mobileNavList", {opener})}>
                        <ul>
                            <li><Link to="/login">Login</Link></li>
                            <li><Link to="/join">Join</Link></li>
                            <li><Link to="/shop">Shopping</Link></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Header;