import React from 'react';
import Header from './Header';
import Footer from './Footer';
import styled from 'styled-components';

const MemberTempBlock = styled.div`
    border-top:80px solid orange;
    .row { padding-top:100px; padding-bottom:100px; 
        h2 { font-size: 1.3rem; text-align:center; color:#888; position:relative;
            padding-bottom:5px; margin-bottom:10px;
            &:before { content:''; position:absolute; bottom:-10px; left:50%; transform:translateX(-50%);
            width:40px; height:2px; background:#888;
        }
        }
    }
`;

const MemberTemp = ({children}) => {
    return (
        <div className="wrap">
            <Header />
            <MemberTempBlock>
                <div className="row">
                    <h2>MEMBERSHIP</h2>
                    {children}
                </div>
            </MemberTempBlock>
            <Footer />
        </div>
    );
};

export default MemberTemp;