import React from 'react';
import styled, { css } from 'styled-components';

const TitBox = styled.div`
    padding-top:150px; text-align:center;
    p { font-size: 1.3rem; text-align:center; color:#888; position:relative;
        padding-bottom:5px; margin-bottom:10px;
        &:before { content:''; position:absolute; bottom:-10px; left:50%; transform:translateX(-50%);
        width:40px; height:2px; background:#888; }
    }
    h2 { font-size:2.4rem; margin:30px 0px; letter-spaicing:-1px }
`;

const CategoryBlock = styled.div`
     .row { display:flex; padding-top:10px; justify-content:center;  }
`;

const Cate = styled.div`
    color:black; background:#eee;
    padding:10px 23px; margin:0 12px; border-radius:50px;
    cursor:pointer;
${
    props => props.active && css`
        color:#fff;
        background:#f64646;
        padding:10px 23px;
        border-radius:50px;
        font-weight:bold;
    `
}

`;


const categories = [
    { name:'all', text:'전체보기'},
    { name:'business', text:'비즈니스'},
    { name:'entertainment', text:'엔터테인먼트'},
    { name:'health', text:'건강'},
    { name:'science', text:'과학'},
    { name:'sports', text:'스포츠'},
    { name:'technology', text:'기술'}
 ]

const Category = ({category, onSelect}) => {
    return (
        <>
            <TitBox>
                <p>CUSTOMER SERVICE</p>
                <h2>NEWS</h2>
            </TitBox>
            <CategoryBlock>
                <div className="row">
                {
                    categories.map( (data) => ( <Cate key={data.name} active={ category === data.name } onClick={()=>onSelect(data.name)}>{data.text}</Cate> ) )
                }
                </div>
            </CategoryBlock>
        </>
    );
};

export default Category;