import React from 'react';
import Slider from "react-slick";
import { IoIosArrowDropleftCircle, IoIosArrowDroprightCircle} from "react-icons/io";
import 'slick-carousel/slick/slick.css';
import './Homesection.scss';

const Homesection = () => {

    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay:true,
        autoplaySpeed:5000,
        prevArrow: <IoIosArrowDropleftCircle />,
        nextArrow:<IoIosArrowDroprightCircle />
    };

    return (
        <div className="homesecWrap">
            <article className="article1">
                <Slider {...settings}>
                    <div className="slide slide1">
                        <div className="text row">
                            <p><em>제철 새우</em>와 함께하는 레시피</p>
                            <h3>매콤쉬림프<br /><em>카레라이스</em></h3>
                            <div className="hashtag">
                                <span>#새우</span>
                                <span>#바몬드카레</span>
                                <span>#카레</span>
                            </div>
                            <button>레시피 바로 보기</button>
                        </div>
                    </div>
                    <div className="slide slide2">
                        <div className="text row">
                            <p><em>빠르고 간편하게</em> 하지만 맛은 최고!</p>
                            <h3>10분 내로<br /><em>스피드 레시피</em></h3>
                            <div className="hashtag">
                                <span>#닭가슴살 스낵랩</span>
                                <span>#새우버터구이</span>
                            </div>
                            <button>스피드 테마 보기</button>
                        </div>
                    </div>
                    <div className="slide slide3">
                        <div className="text row">
                            <p><em>쉽고 빠르게</em> 따라 할 수 있는!</p>
                            <h3>새내기 요리사<br /><em>초보 레시피</em></h3>
                            <div className="hashtag">
                                <span>#토마토냉파스타</span>
                                <span>#에그마요샌드위치</span>
                            </div>
                            <button>왕초보 테마 보기</button>
                        </div>
                    </div>
                </Slider>
            </article>
            <article className="article2">
                <div className="row">
                    <div className="at1Wrap">
                        <div className="box1 box">
                            <h3>이건 어때요?<br />인기 검색어</h3>
                            <span>고민을 덜어주는 추천 검색어</span>
                            <div className="tagbox">
                                <p>#카레</p><br />
                                <p>#마요네즈</p><br />
                                <p>#분식</p><br />
                                <p>#브런치</p><br />
                                <p>#집밥</p><br />
                                <p>#치즈듬뿍</p><br />
                                <p>#캠핑</p><br />
                                <p>#간단 꿀조합</p>
                            </div>
                        </div>
                        <div className="box2 box">
                            <em>kid's Cooking</em>
                            <h3>키즈셰프 레시피</h3>
                            <div className="img"></div>
                            <p>옥수수푸딩</p>
                            <span>#옥수수푸딩 #콘스프</span>
                        </div>
                        <div className="box3 box">
                            <em>History</em>
                            <h3>최근 본 레시피</h3>
                            <span>최근에 본 레시피를<br />다시 찾아볼 수 있습니다.</span>
                            <div className="img"></div>
                            <span>#카레맛집 #새우 #스리라차소스</span>
                            <p>매콤 쉬림프 카레라이스</p>
                        </div>
                        <div className="box4 box">
                            <h3>오뚜기의<br />쉽고 간단한<br />계량 꿀팁!</h3>
                            <span>밥숟가락, 종이컵으로<br />간단하고 쉽게 알려드립니다.</span>
                        </div>
                    </div>
                </div>
            </article>
            <article className="article3">
                <div className="txtBox">
                    <p><strong>요리를 더 쉽게</strong><br />오뚜기몰의 각종 소스들로 모든 레시피를 보다<br /><em>쉽게, 빠르게, 맛있게</em>
                    </p>
                    <button>오뚜기몰 가기</button>
                </div>
            </article>
            <article className="article4">
                <div className="row">
                    <div className="titBox">
                        <h2>추천레시피</h2>
                        <p>오뚜기가 제안하는 레시피로 최고의 순간을 만들어 보세요.</p>
                    </div>
                    <div className="recommWrap">
                        <div className="left">
                            <div className="top">
                                <div className="img"></div>
                                <div className="txt">
                                    <span>#3일숙성카레 #카레맛집 #쇠고기</span>
                                    <h3>카레 비프 스튜</h3>
                                </div>
                            </div>
                            <div className="bottom">
                                <div className="b_left">
                                    <div className="img"></div>
                                    <div className="txt">
                                        <span>#파우치스프X오뚜기밥 #리조또 #양송이</span>
                                        <h3>버섯크림리조또</h3>
                                    </div>
                                </div>
                                <div className="b_right">
                                    <div className="img"></div>
                                    <div className="txt">
                                        <span>#셰프의팁 #즉석국 #캠핑</span>
                                        <h3>차돌박이 된장찌개</h3>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="right">
                            <div className="top">
                                <div className="t_left">
                                    <div className="img"></div>
                                    <div className="txt">
                                        <span>#핫케이크 #시나몬 #브런치</span>
                                        <h3>더치베이비 팬케이크</h3>
                                    </div>
                                </div>
                                <div className="t_right">
                                    <div className="img"></div>
                                    <div className="txt">
                                        <span>#집밥 #밑반찬 #꿀유자차</span>
                                        <h3>유자 연근조림</h3>
                                    </div>
                                </div>
                            </div>
                            <div className="bottom">
                                <div className="img"></div>
                                <div className="txt">
                                    <span>#갈릭아이올리 #계란요리 #브런치</span>
                                    <h3>에그마요 샌드위치</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </article>
            <article className="article5">
                <div className="row">
                    <div className="titBox">
                        <h2>셰프의 팁 간편식 업그레이드</h2>
                        <p>오뚜기 간편식이 근사한 요리가 되는 팁을 공개합니다.</p>
                    </div>
                    <div className="contWrap">
                        <ul>
                            <li>
                                <div className="img"></div>
                                <p>#볶음밥X라조장 #계란요리 #치즈듬뿍</p>
                                <h3>한라산볶음밥1</h3>
                            </li>
                            <li>
                                <div className="img"></div>
                                <p>#볶음밥X라조장 #계란요리 #치즈듬뿍</p>
                                <h3>한라산볶음밥2</h3>
                            </li>
                            <li>
                                <div className="img"></div>
                                <p>#볶음밥X라조장 #계란요리 #치즈듬뿍</p>
                                <h3>한라산볶음밥3</h3>
                            </li>
                        </ul>
                        <button>더보기</button>
                    </div>
                </div>
            </article>
        </div>
    );
};

export default Homesection;         